import * as actions from 'constants/actions/StatisticsActionsConstants';

export function addWaitingTime(payload) {
    return {type: actions.ADD_WAITING_TIME, payload}
}

export function incrementProcessorIdleTimes() {
    return {type: actions.INCREMENT_PROCESSOR_IDLE_TIMES}
}


