import * as actions from 'constants/actions/ProcessorActionsConstants';

const initialState = {
    processTime: undefined,
    idleTimes: 0
};

export default function (state = initialState, {type, payload}) {
    let newState;

    switch (type) {
        case actions.DECREMENT_PROCESS_TIME:
            newState = {...state, processTime: payload};
            break;
        case actions.INCREMENT_IDLE_TIMES:
            newState = {...state, idleTimes: state.idleTimes + 1};
            break;
        default:
            newState = state;
            break;
    }

    return newState;
}


