import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as timerActions from "store/actions/TimerActions";
import {Button, Icon} from 'semantic-ui-react';

class Timer extends Component {
    render() {
        return (
            <div className={this.props.className}>
                <div className='timer__controls'>
                    <Button disabled={this.props.tact < 1} onClick={this.onSlower}>
                        <span><Icon name='backward'/>&nbsp;</span>
                    </Button>
                    <Button disabled={this.props.tact < 1} onClick={this.onFaster}>
                        <span>&nbsp;<Icon name='forward'/></span>
                    </Button>
                    <Button onClick={this.onStop}>{
                        this.props.tact === 0 ?
                            <span> <Icon name='play'/></span> : (this.props.isPause ?
                            <span> <Icon name='play'/></span> : <span>&nbsp;<Icon name='stop'/></span>)
                    }</Button>
                </div>
            </div>
        )
    }

    shouldComponentUpdate(nextProps) {
        return this.props.tact !== nextProps.tact ||
               this.props.speed !== nextProps.speed ||
               this.props.isPause !== nextProps.isPause;
    }

    componentWillUpdate(nextProps) {
        const isContinue = (this.props.isPause && !nextProps.isPause);
        const isPaused = (!this.props.isPause && nextProps.isPause);
        const isChangeSpeed = (this.props.speed !== nextProps.speed && !this.props.isPause);

        isContinue && this.startTimer();

        isPaused && this.stopTimer();

        !isPaused && !isContinue && isChangeSpeed && this.reloadTimer();
    }

    initializingTheTimer = (speed) => {
        return setInterval(() => {
            this.props.incrementTact();
        }, speed);
    };

    startTimer = () => {
        const idTimer = this.initializingTheTimer(this.props.speed * 1000);
        this.props.setIdTimer(idTimer);
    };

    stopTimer = () => {
        clearInterval(this.props.idTimer);
    };

    reloadTimer = () => {
        this.stopTimer();
        this.startTimer();
    };

    onSlower = () => {
        this.props.changeSpeedTimer(this.props.speed + 0.1);
    };

    onFaster = () => {
        if (this.props.speed > .2) {
            this.props.changeSpeedTimer(this.props.speed - 0.1);
        }
    };

    onStop = () => {
        this.props.isPauseTimer(!this.props.isPause);
    };
}

function mapStateToProps(state) {
    return {
        tact: state.timer.tact,
        speed: state.timer.speed,
        isPause: state.timer.isPause,
        idTimer: state.timer.idTimer
    }
}

function mapDispatchToProps(dispatch) {
    return {
        incrementTact: bindActionCreators(timerActions.incrementTact, dispatch),
        changeSpeedTimer: bindActionCreators(timerActions.changeSpeed, dispatch),
        isPauseTimer: bindActionCreators(timerActions.isPause, dispatch),
        setIdTimer: bindActionCreators(timerActions.setIdTimer, dispatch)
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Timer);



