import React, {Component} from 'react';
import {Button, Form, Header, Icon, Input, Label, Modal} from "semantic-ui-react";
import './generation-process-form.css';

class GenerationProcessForm extends Component {
    render() {
        return (
            <Form className={this.props.className} onSubmit={this.props.onSubmit}>
                <div className='generation-process__label'>
                    <div className='generation-process__settings'>
                        <Modal trigger={<Icon name='setting' size='large'/>}>
                            <Modal.Header>  </Modal.Header>
                            <Modal.Content>
                                <Modal.Description>
                                    <Header>Default Profile Image</Header>
                                    <p>We've found the following gravatar image associated with your e-mail address.</p>
                                    <p>Is it okay to use this photo?</p>
                                </Modal.Description>
                            </Modal.Content>
                        </Modal>
                    </div>
                    <Label
                        htmlFor="processes-count"> () ():
                    </Label>
                    <Input
                        id='processes-count'
                        name='count'
                        type="number"
                        placeholder='- '
                    />
                </div>
                <div className='field'>
                    <Button
                        fluid
                        disabled={this.props.tact < 1}
                        type='submit'></Button>
                </div>
            </Form>
        )
    }
}

export default GenerationProcessForm;


