import React, {Component} from 'react';
import Queue from 'components/Queue';
import Processor from 'components/Processor';
import Memory from 'components/Memory';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {addProcessToCompleteQueue, removeFirstReadyProcess, sortingByPriority, updatePriority} from 'store/actions/QueueActions';
import {decrementTactToSorting, freeUpMemory, setCurrentProcess, deleteCurrentProcess} from 'store/actions/SchedulerActions';
import {addWaitingTime} from 'store/actions/StatisticsActions';
import './scheduler.css';

class Scheduler extends Component {
    render() {
        return (
            <div className='scheduler'>
                <div>
                    <div className='scheduler__content'>
                        <Processor
                            waitingTime={this.props.statistics.waitingTime}
                            countCompletedProcesses={this.props.statistics.countCompletedProcesses}
                            className='processor'
                            completedProcess={this.onCompleteProcess}/>
                        <Memory
                            className='memory'
                            memory={this.props.schedule.memory}/>
                    </div>
                    <div className='queue-list'>
                        <Queue
                            title='    '
                            processes={this.props.queue.readyProcesses}/>
                        <Queue
                            title=' '
                            processes={this.props.queue.completedProcesses}/>
                        <Queue
                            title=' '
                            processes={this.props.queue.rejectedProcesses}/>
                    </div>
                </div>
            </div>
        )
    }

    shouldComponentUpdate(nextProps) {
        return this.props.tact !== nextProps.tact;
    }

    componentWillUpdate(nextProps) {
        const props = this.props;

        if (props.tact !== nextProps.tact) {
            (this.props.queue.readyProcesses[0] !== undefined) && (props.decrementTactToSorting());

            if (props.schedule.currentProcess === undefined && props.queue.readyProcesses[0] !== undefined) {
                props.setCurrentProcess(props.queue.readyProcesses[0]);
                props.removeFirstFromQueue();
            }

            if (props.schedule.tactToSortingQueue === 0 && !props.queue.readyProcessesIsSorted) {
                props.sortingProcessesByPriority(props.queue.readyProcesses);
                props.updatePriority(props.queue.readyProcesses, props.tact);
            }
        }
    }

    onCompleteProcess = (process) => {
        this.props.freeUpMemory(process.memory);
        this.props.addProcessToCompleteQueue(process);
        this.props.addWaitingTime(process.timeEnd - process.startTime);
        this.props.deleteCurrentProcess();
    };
}

function mapStateToProps(state)     {
    return {
        schedule: {
            tactToSortingQueue: state.scheduler.tactToSortingQueue,
            currentProcess: state.scheduler.currentProcess,
            memory: state.scheduler.memory,
        },
        queue: {
            readyProcesses: state.queue.readyProcesses,
            completedProcesses: state.queue.completedProcesses,
            rejectedProcesses: state.queue.rejectedProcesses,
            readyProcessesIsSorted: state.queue.readyProcessesIsSorted
        },
        statistics: {
            countCompletedProcesses: state.statistics.countCompletedProcesses,
            waitingTime: state.statistics.waitingTime
        }
    }
}

function mapDispatchToProps(dispatch) {
    return {
        decrementTactToSorting: bindActionCreators(decrementTactToSorting, dispatch),
        sortingProcessesByPriority: bindActionCreators(sortingByPriority, dispatch),
        setCurrentProcess: bindActionCreators(setCurrentProcess, dispatch),
        removeFirstFromQueue: bindActionCreators(removeFirstReadyProcess, dispatch),
        addProcessToCompleteQueue: bindActionCreators(addProcessToCompleteQueue, dispatch),
        freeUpMemory: bindActionCreators(freeUpMemory, dispatch),
        deleteCurrentProcess: bindActionCreators(deleteCurrentProcess, dispatch),
        updatePriority: bindActionCreators(updatePriority, dispatch),
        addWaitingTime: bindActionCreators(addWaitingTime, dispatch)
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Scheduler);


