import React, {Component} from 'react';
import {toInt} from "helpers";
import {Card, Progress, Statistic} from "semantic-ui-react";
import './memory.css'

class Memory extends Component {
    render() {
        const percentAllocateMemory = toInt(100 - (this.props.memory / 1024000 * 100));

        return (
            <Card className={this.props.className}>
                <Card.Content className='memory__container'>
                    <div className='memory__text'>: </div>
                    <Statistic
                        value={toInt(1024 - this.props.memory / 1000)}
                        label=''
                        size='small'
                        className='memory__num'
                    />
                </Card.Content>
                <Card.Content className='memory_percent'>
                    <Progress
                        color={percentAllocateMemory < 750 ? 'green' : percentAllocateMemory < 900 ? 'orange' : 'red'}
                        size='large'
                        percent={percentAllocateMemory}
                        progress
                    ></Progress>
                </Card.Content>
            </Card>
        )
    }
}

export default Memory;


