import * as actions from 'constants/actions/SchedulerActionsConstants';

export function decrementTactToSorting() {
    return {type: actions.DECREMENT_TACT_TO_SORTING}
}

export function setCurrentProcess(payload) {
    return {type: actions.SET_CURRENT_PROCESS, payload}
}

export function deleteCurrentProcess () {
    return {type: actions.DELETE_CURRENT_PROCESS}
}

export function updateCurrentProcess(payload) {
    return {type: actions.UPDATE_CURRENT_PROCESS, payload}
}

export function allocateMemory(payload) {
    return {type: actions.ALLOCATE_MEMORY, payload}
}

export function freeUpMemory(payload) {
    return {type: actions.FREE_UP_MEMORY, payload}
}


