import React, {Component} from 'react';
import Timer from 'components/Timer';
import Scheduler from 'containers/Scheduler';
import GenerationProcessForm from "components/GenerationProcessForm/GenerationProcessForm";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";
import {randomIntNum} from "helpers/random";
import {Statistic, Container} from 'semantic-ui-react';
import {
    addProcessToReadyQueue,
    addProcessToRejectQueue
} from "store/actions/QueueActions";
import {allocateMemory} from "store/actions/SchedulerActions";
import * as processStatuses from "constants/process-statuses";
import './App.css';

/*
      - HPF
       - 2
        - +
       - +
*/

class App extends Component {
    render() {
        return (
            <Container className="app">
                <div className='header'>
                    <div className='timer-container'>
                        <Statistic label='' value={this.props.timer.tact}/>
                        <Timer className='timer'/>
                    </div>
                    <GenerationProcessForm
                        className='generation-process'
                        tact={this.props.tact}
                        onSubmit={this.onGeneration}/>
                </div>
                <Scheduler tact={this.props.timer.tact}/>
            </Container>
        );
    }

    onGeneration = (event) => {
        event.preventDefault();
        let process;
        let allMemory = 0;

        for (let i = 0; i < event.target.count.value; i++) {
            process = {
                pid: randomIntNum(0, 10000),
                priority: randomIntNum(1, 10),
                time: randomIntNum(10, 20),
                memory: randomIntNum(10, 10000),
                startTime: this.props.timer.tact,
                status: processStatuses.READY,
                timeEnd: undefined
            };

            allMemory += process.memory;

            if (allMemory <= this.props.memory) {
                this.props.allocateMemory(process.memory);
                this.props.addProcessToReadyQueue(process);
            } else {
                this.props.addProcessToRejectQueue({...process, status: processStatuses.REJECTET});
            }
        }
    };
}

function mapStateToProps(state) {
    return {
        timer: {
            tact: state.timer.tact
        },
        memory: state.scheduler.memory
    }
}

function mapDispatchToProps(dispatch) {
    return {
        addProcessToReadyQueue: bindActionCreators(addProcessToReadyQueue, dispatch),
        addProcessToRejectQueue: bindActionCreators(addProcessToRejectQueue, dispatch),
        allocateMemory: bindActionCreators(allocateMemory, dispatch)
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);



