import React, {Component} from 'react';
import './Queue.css';
import {Table, Header} from 'semantic-ui-react'

class Queue extends Component {
    render() {
        return (
            <div className='queue'>
                <Header as='h3' className='queue__header'>{this.props.title} <br/>: {this.props.processes.length}</Header>
                <Table celled>
                    <Table.Header>
                        <Table.Row>
                            <Table.HeaderCell>PID</Table.HeaderCell>
                            <Table.HeaderCell><br/>()</Table.HeaderCell>
                            <Table.HeaderCell><br/>()</Table.HeaderCell>
                            <Table.HeaderCell></Table.HeaderCell>
                            <Table.HeaderCell></Table.HeaderCell>
                        </Table.Row>
                    </Table.Header>
                    <Table.Body>
                        {this.props.processes.map((process, index) => {
                            return (
                                <Table.Row key={index}>
                                    <Table.Cell>{process.pid}</Table.Cell>
                                    <Table.Cell>{process.memory} </Table.Cell>
                                    <Table.Cell>{process.time}</Table.Cell>
                                    <Table.Cell>{process.priority}</Table.Cell>
                                    <Table.Cell>{process.status}</Table.Cell>
                                </Table.Row>
                            );
                        })}
                    </Table.Body>
                </Table>
            </div>
        )
    }
}

export default Queue;


