import * as actions from 'constants/actions/StatisticsActionsConstants';

const initialState = {
    countCompletedProcesses: 0,
    waitingTime: 0,
    processorIdleTimes: 0
};

export default function (state = initialState, {type, payload}) {
    let newState;

    switch (type) {
        case actions.ADD_WAITING_TIME:
            newState = {
                ...state,
                waitingTime: state.waitingTime + payload,
                countCompletedProcesses: state.countCompletedProcesses + 1
            };
            break;
        case actions.INCREMENT_PROCESSOR_IDLE_TIMES:
            newState = {...state, processorIdleTimes: state.processorIdleTimes + 1};
            break;
        default:
            newState = state;
    }

    return newState;
}


