import * as actions from 'constants/actions/QueueConstantsActions';

const maxCountTact = 50;

export function sortingByPriority(processes) {
    processes.sort((first, second) => {
        return first.priority - second.priority;
    });

    return {type: actions.SORTING_READY_PROCESSES, payload: processes};
}

export function updatePriority(processes, currentTact) {
     const payload = processes.map(process => {
        if (currentTact - process.startTime > maxCountTact && process.priority !== 1) {
            return {...process, priority: process.priority - 1}
        } else {
            return process;
        }
    });

    return {type: actions.UPDATE_PRIORITY, payload};
}

export function removeFirstReadyProcess() {
    return {type: actions.REMOVE_FIRST_READY_PROCESS}
}

export function addProcessToReadyQueue(payload) {
    return {type: actions.ADD_PROCESS_TO_READY_QUEUE, payload};
}

export function addProcessToCompleteQueue(payload) {
    return {type: actions.ADD_PROCESS_TO_COMPLETED_QUEUE, payload};
}

export function addProcessToRejectQueue(payload) {
    return {type: actions.ADD_PROCESS_TO_REJECTED_QUEUE, payload};
}



