import * as actions from 'constants/actions/TimerConstantsActions';

export function incrementTact() {
    return {type: actions.INCREMENT_TACT}
}

export function changeSpeed(payload) {
    return {type: actions.CHANGE_SPEED, payload}
}

export function isPause(payload) {
    return {type: actions.IS_PAUSE, payload}
}

export function setIdTimer(payload) {
    return {type: actions.SET_ID_TIMER, payload}
}


