import * as actions from 'constants/actions/TimerConstantsActions'

const initialState = {
    tact: 0,
    speed: .4,
    isPause: true,
    idTimer: 0
};

export default function (state = initialState, {type, payload}) {
    let newState;

    switch (type) {
        case actions.INCREMENT_TACT:
            newState = {...state, tact: state.tact + 1};
            break;
        case actions.CHANGE_SPEED:
            newState = {...state, speed: payload};
            break;
        case actions.IS_PAUSE:
            newState = {...state, isPause: payload};
            break;
        case actions.SET_ID_TIMER:
            newState = {...state, idTimer: payload};
            break;
        default: newState = state;
    }

    return newState;
}


