import * as actions from 'constants/actions/SchedulerActionsConstants';

const defaultTact = 20;

const initialState = {
    priorityTime: 0,
    tactToSortingQueue: defaultTact,
    currentProcess: undefined,
    memory: 1024000
};

export default function (state = initialState, {type, payload}) {
    let newState;

    switch (type) {
        case actions.DECREMENT_TACT_TO_SORTING:
            newState = {
                ...state,
                tactToSortingQueue: state.tactToSortingQueue === 0 ? defaultTact : state.tactToSortingQueue - 1
            };
            break;
        case actions.SET_CURRENT_PROCESS:
            newState = {...state, currentProcess: payload};
            break;
        case actions.UPDATE_CURRENT_PROCESS:
            newState = {...state, currentProcess: {...state.currentProcess, ...payload}};
            break;
        case actions.ALLOCATE_MEMORY:
            newState = {...state, memory: state.memory - payload};
            break;
        case actions.FREE_UP_MEMORY:
            newState = {...state, memory: state.memory + payload};
            break;
        case actions.DELETE_CURRENT_PROCESS:
            newState = {...state, currentProcess: undefined};
            break;
        default: newState = state
    }

    return newState;
}


