import React, {Component} from 'react';
import {Popup, Card} from "semantic-ui-react";

class DescriptionProcess extends Component {
    render() {
        const {process, processTime} = this.props;
        
        if (process !== undefined) {
            return (
                <Popup
                    size='large'
                    trigger={
                        <Card.Description>{" (PID) : " + process.pid}</Card.Description>
                    }>
                    <Popup.Content>
                        <div>: {process.memory} </div>
                        <div>  : {processTime}</div>
                        <div>  : {process.time}</div>
                        <div>: {process.priority}</div>
                        <div>    : {process.startTime}</div>
                        <div>: {process.status}</div>
                    </Popup.Content>
                </Popup>
            )
        } else {
            return (
                <Card.Description> </Card.Description>
            )
        }
    }
}

export default DescriptionProcess;


