import React, {Component} from 'react';
import {connect} from "react-redux";
import {bindActionCreators} from 'redux';
import {updateCurrentProcess} from 'store/actions/SchedulerActions';
import {decrementProcessTime} from 'store/actions/ProcessorActions';
import {incrementProcessorIdleTimes} from 'store/actions/StatisticsActions';
import * as processStatuses from 'constants/process-statuses';
import {Button, Card, Image, Modal} from 'semantic-ui-react';
import DescriptionProcess from './DescriptionProcess';

class Processor extends Component {
    render() {
        const urlImg = 'https://upload.wikimedia.org/wikipedia/commons/thumb/6/6c/Intel_CPU_Core_' +
            'i7_2600K_Sandy_Bridge_top.jpg/800px-Intel_CPU_Core_i7_2600K_Sandy_Bridge_top.jpg';

        return (
            <Card className={this.props.className}>
                <Card.Content>
                    <Image floated='right' size='mini' src={urlImg}/>
                    <Card.Header>
                        
                    </Card.Header>
                    <Card.Meta>
                        200 
                    </Card.Meta>
                    <DescriptionProcess process={this.props.process} processTime={this.props.processTime}/>
                </Card.Content>
                <Card.Content extra>
                    <div className='ui two buttons'>
                        <Modal
                            trigger={<Button disabled={this.props.tact === 0} basic color='green'></Button>}>
                            <Modal.Header>Select a Photo</Modal.Header>
                            <Modal.Content>
                                <div>  : {this.props.waitingTime / this.props.countCompletedProcesses} </div>
                                <div>  : {this.props.idleTimes} </div>
                            </Modal.Content>
                        </Modal>
                    </div>
                </Card.Content>
            </Card>
        )
    }

    shouldComponentUpdate(nextProps) {
        return (this.props.tact !== nextProps.tact)
    }

    componentWillUpdate() {
        if (this.props.process !== undefined) {
            if (this.props.processTime === undefined) {
                this.props.updateProcess({status: processStatuses.PERFORMED});
                this.props.decrementTimeProcess(this.props.process.time);
            } else if (this.props.processTime === 0) {
                this.props.completedProcess({
                    ...this.props.process,
                    timeEnd: this.props.tact,
                    status: processStatuses.COMPLETE
                });
                this.props.decrementTimeProcess(undefined);
            } else {
                this.props.decrementTimeProcess(this.props.processTime - 1);
            }
        } else {
            this.props.incrementProcessorIdleTimes();
        }
    }
}

function mapStateToProps(state) {
    return {
        process: state.scheduler.currentProcess,
        tact: state.timer.tact,
        processTime: state.processor.processTime,
        idleTimes: state.statistics.processorIdleTimes
    }
}

function mapDispatchToProps(dispatch) {
    return {
        updateProcess: bindActionCreators(updateCurrentProcess, dispatch),
        decrementTimeProcess: bindActionCreators(decrementProcessTime, dispatch),
        incrementProcessorIdleTimes: bindActionCreators(incrementProcessorIdleTimes, dispatch)
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Processor);


