import * as actions from 'constants/actions/QueueConstantsActions';

const initialState = {
    readyProcessesIsSorted: true,
    readyProcesses: [],
    completedProcesses: [],
    rejectedProcesses: []
};

export default function (state = initialState, {type, payload}) {
    let newState;

    switch (type) {
        case actions.SORTING_READY_PROCESSES:
            newState = {...state, readyProcesses: payload, readyProcessesIsSorted: true};
            break;
        case actions.REMOVE_FIRST_READY_PROCESS:
            newState = {...state, readyProcesses : state.readyProcesses.slice(1)};
            break;
        case actions.ADD_PROCESS_TO_READY_QUEUE:
            newState = {...state, readyProcesses: [...state.readyProcesses, payload], readyProcessesIsSorted: false};
            break;
        case actions.ADD_PROCESS_TO_COMPLETED_QUEUE:
            newState = {...state, completedProcesses: [...state.completedProcesses, payload]};
            break;
        case actions.ADD_PROCESS_TO_REJECTED_QUEUE:
            newState = {...state, rejectedProcesses: [...state.rejectedProcesses, payload]};
            break;
        case actions.UPDATE_PRIORITY:
            newState = {...state, readyProcesses: payload};
            break;
        default:
            newState = state;
            break;
    }

    return newState;
}


